@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@400;700;900&display=swap');

html {
  font-size: 10px;
}

* {
  box-sizing: border-box;
  font-family: 'Nunito', sans-serif;
}

body {
  margin: 0;
  padding: 0;
  background-color: rgb(244,244,244);
  color: #333;
}

p {
  font-size: 1.6rem;
}

small {
  font-size: 1.2rem;
}

.App {
  margin: 0 auto;
  max-width: 1000px;
  padding: 40px;
}

.title {
  font-size: 4rem;
  text-align: center;
  text-transform: uppercase;
  font-family: 'Nunito', sans-serif;
  font-weight: 900;
}

.form {
  display: grid;
}

.lable {
  font-size: 1.4rem;
  text-transform: uppercase;
  margin-bottom: .3rem;
}

input {
  background-color: #ddd;
  padding: 1rem 2rem;
  font-size: 1.6rem;
  border-radius: 20px;
  border: 1px solid #ddd;
  line-height: 2.8px;
  margin-bottom: .8rem;
  color: #333;
}

button {
  padding: 1rem 2rem;
  background-color: rgba(0,0,0,0.75);
  color: #ddd;
  font-size: 1.4rem;
  border-radius: 20px;
  border: 1px solid rgba(0,0,0,0.75) ;
  transition: background-color 250ms;
  cursor: pointer;
}

button:hover {
  background-color: rgba(0,0,0,0.8);
}

@media (min-width: 750px) {
  .form {
    grid-template-columns: auto 1fr auto;
    grid-gap: 1rem;
    align-items: center;
  }
}

.cardList {
  margin-top: 1rem;
}

.card {
  padding: 2rem;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 1px 1px 3px rgba(0,0,0,0.15);
  margin-bottom: 2rem;
}

.card--title {
  font-size: 3.2rem;
  letter-spacing: 2px;
  text-align: center;
}

.card--image {
  display: block;
  margin: 0 auto;
}

.card--desc {
  font-size: 1.8rem;
  letter-spacing: 1px;
  line-height: 1.2;
}

